import React from 'react';
import { useTitle } from '../../utils/utils';
const PrivacyPolicy = () => {
    useTitle("Privacy Policy");

    return (
        <React.Fragment>
            <section className="section">
                <div className="container">


                    <h1 class="title">Privacy Policy</h1>


                    <p><strong>Effective Date: 24-11-2023</strong></p>
                    <h3>Your privacy is important to us</h3>


                    <p className="mt-2">It is SAC Code-Gen's policy to respect your privacy regarding any information we may collect while operating our website. This Privacy Policy applies to <a href="http://sacgencode.js.org"> sacgencode.js.org</a> (hereinafter, "us", "we", or "sacgencode.js.org"). We respect your privacy and are committed to protecting personally identifiable information you may provide us through the Website. We have adopted this privacy policy ("Privacy Policy") to explain what information may be collected on our Website, how we use this information, and under what circumstances we may disclose the information to third parties. This Privacy Policy applies only to information we collect through the Website and does not apply to our collection of information from other sources.</p>
                    <p className='mt-2'>This Privacy Policy, together with the Terms of service posted on our Website, set forth the general rules and policies governing your use of our Website. Depending on your activities when visiting our Website, you may be required to agree to additional terms of service.</p><h2 className="title mt-5" id="tableofcontents">Contents</h2>

                    <ol type="1">
                        <li><a href="#websitevisitors"><strong>Website Visitors</strong></a>
                        </li>
                        <li><a href="#Security"><strong>Security</strong></a>
                        </li>
                        <li><a href="#ExternalLinks"><strong>Links To External Sites</strong></a>
                        </li>
                        <li><a href="#Cookies"><strong>Cookies</strong></a>
                        </li>
                        <li><a href="#Changes"><strong>Privacy Policy Changes</strong></a>
                        </li>
                        <li><a href="#Credit"><strong>Contact Information &amp; Credit</strong></a>
                        </li>

                    </ol>
                    <h2 className="title mt-5" id="websitevisitors">1. Website Visitors</h2>
                    <p>Like most website operators, SAC Code-Gen collects non-personally-identifying information of the sort that web browsers and servers typically make available, such as the browser type, language preference, referring site, and the date and time of each visitor request. SAC Code-Gen's purpose in collecting non-personally identifying information is to better understand how SAC Code-Gen's visitors use its website. From time to time, SAC Code-Gen may release non-personally-identifying information in the aggregate, e.g., by publishing a report on trends in the usage of its website.</p>
                    <p className='mt-2'>SAC Code-Gen also collects potentially personally-identifying information like Internet Protocol (IP) addresses for logged in users and for users leaving comments on http://sacgencode.js.org blog posts. SAC Code-Gen only discloses logged in user and commenter IP addresses under the same circumstances that it uses and discloses personally-identifying information as described below.</p>

                    <h2 className="title mt-5" id="Security">2. Security</h2>
                    <p>The security of your Personal Information is important to us, but remember that no method of transmission over the Internet, or method of electronic storage is 100% secure. While we strive to use commercially acceptable means to protect your Personal Information, we cannot guarantee its absolute security.</p>

                    <h2 className="title mt-5" id="ExternalLinks">3. Links To External Sites</h2>
                    <p>Our Service may contain links to external sites that are not operated by us. If you click on a third party link, you will be directed to that third party's site. We strongly advise you to review the Privacy Policy and terms of service of every site you visit.</p>
                    <p className='mt-2'>We have no control over, and assume no responsibility for the content, privacy policies or practices of any third party sites, products or services.</p>

                    <h2 className="title mt-5" id="Cookies">4. Cookies</h2>
                    <p>To enrich and perfect your online experience, SAC Code-Gen uses "Cookies", similar technologies and services provided by others to display personalized content, appropriate advertising and store your preferences on your computer.</p>
                    <p className='mt-2'>A cookie is a string of information that a website stores on a visitor's computer, and that the visitor's browser provides to the website each time the visitor returns. SAC Code-Gen uses cookies to help SAC Code-Gen identify and track visitors, their usage of http://sacgencode.js.org, and their website access preferences. SAC Code-Gen visitors who do not wish to have cookies placed on their computers should set their browsers to refuse cookies before using SAC Code-Gen's websites, with the drawback that certain features of SAC Code-Gen's websites may not function properly without the aid of cookies.</p>
                    <p className='mt-2'>By continuing to navigate our website without changing your cookie settings, you hereby acknowledge and agree to SAC Code-Gen's use of cookies.</p>

                    <h2 className="title mt-5" id="Changes">5. Privacy Policy Changes</h2><p>Although most changes are likely to be minor, SAC Code-Gen may change its Privacy Policy from time to time, and in SAC Code-Gen's sole discretion. SAC Code-Gen encourages visitors to frequently check this page for any changes to its Privacy Policy. Your continued use of this site after any change in this Privacy Policy will constitute your acceptance of such change.</p>

                    <h2 className="title mt-5" id="Credit">6. Contact Information &amp; Credit</h2>
                    <p>If you have any questions about our Privacy Policy, please contact us via <a href="mailto:itsrohitofficial@gmail.com">email</a>.</p>
                </div>
            </section>
        </React.Fragment >
    );
}

export default PrivacyPolicy;